import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AprobadorPage } from './aprobador.page';

const routes: Routes = [
  {
    path: 'aprobador',
    component: AprobadorPage,
    children: [
      {
        path: 'revision',
        loadChildren: () =>
          import('./revision/revision.module').then(m => m.RevisionPageModule)
      },
      {
        path: 'historial',
        loadChildren: () =>
          import('./historial/historial.module').then(m => m.HistorialPageModule)
      }

    ]
  },
  {
    path: '',
    redirectTo: 'aprobador/revision',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AprobadorPageRoutingModule {}
